.inputColor {
    width: 45px;
    height: 36px;
    background-color: #fff;
    border: none;
    cursor: pointer;
    margin-right: 1.5rem;

}

input[type="color"]::-webkit-color-swatch-wrapper {
    padding: 0;
}

input[type="color"]::-webkit-color-swatch {
    border: none;
    border-radius: 5px;
}
.inputColorl {
    width: 40px;
    height: 36px;
    background-color: #fff;
    border: none;  
    margin-right: 2rem;
}